<template>
  <div class="game-sort-content">
    <nav class='navList spaceBetween'>
      <dl class="nav-item active">
        <dt><img style='width: 44px;' src="@/assets/img/charactor@3x.png" alt=""></dt>
        <dd>角色</dd>
      </dl>
      <dl class="nav-item">
        <dt><img style='width: 60px;' src="@/assets/img/play@3x.png" alt=""></dt>
        <dd>休闲</dd>
      </dl>
      <dl class="nav-item">
        <dt><img style='width: 58px;' src="@/assets/img/standAlone@3x.png" alt=""></dt>
        <dd>单机</dd>
      </dl>
      <dl class="nav-item">
        <dt><img style='width: 48px;' src="@/assets/img/card@3x.png" alt=""></dt>
        <dd>棋牌</dd>
      </dl>
      <dl class="nav-item">
        <dt><img style='width: 40px;' src="@/assets/img/action@3x.png" alt=""></dt>
        <dd>动作</dd>
      </dl>
      <dl class="nav-item">
        <dt><img style='width: 56px;' src="@/assets/img/shoot@3x.png" alt=""></dt>
        <dd>射击</dd>
      </dl>
    </nav>
    <div class='banner' v-lazy:background-image="bannerBg">
      <div class="banner-desc spaceBetween">
        <a href='javascript: void(0);' class="banner-item">
          <h3 class='oneLine'>企鹅电竞杯竞技大礼</h3>
          <p>王者荣耀</p>
        </a>
        <a href='javascript: void(0);' class="banner-item">
          <h3 class='oneLine'>连续签到领取888金币</h3>
          <p>火影忍者</p>
        </a>
      </div>
    </div>
    <item-list></item-list>
  </div>
</template>

<script>
import ItemList from './ItemList'
export default {
  name: 'gameSort',
  data () {
    return {
      bannerBg: 'https://dakani1.github.io/interview.github.io/static/img/banner@3x.8bf886d.png'
    }
  },
  components: {
    'item-list': ItemList
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '~@/assets/var.scss';
.navList{
  width: 100%;
  dl{
    flex: 1;
    position: relative;
    padding: 26px 0;
    border-bottom: 1px dotted $borderColor;
    &.active:after{
      content: ' ';
      display: block;
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: 5px;
      background-color: $activeColor;
    }
    dt{
      height: 48px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin-bottom: 18px;
    }
    dd{
      font-size: 22px;
      line-height: 29px;
      letter-spacing: 0px;
      color: $subTitleColor;
    }
  }
}
.banner{
  background: url('~@/assets/img/banner@3x.png') center top no-repeat;
  background-size: 100%;
  .banner-desc{
    text-align: left;
    .banner-item{
      flex: 1;
      padding: 230px 28px 40px;
      border-bottom: 1px dotted $borderColor;
      display: block;
      h3{
        font-size: 32px;
        font-weight: normal;
        line-height: 36px;
        letter-spacing: 0px;
        color: $titleColor;
      }
      p{
        font-size: 24px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 24px;
        color: $subTitleColor;
        margin-top: 15px;
      }
      & + .banner-item{
        border-left: 1px dotted $borderColor;
      }
    }
  }
}

</style>



